<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal choose-network-modal" @click.stop="">
            <div class="new-modal-back-button-container">
                <button class="new-modal-back-button" @click="closeModal"></button>
            </div>
            <div class="modal-content">
                <div class="title">{{$localizeFilter('fill_up_a_wallet')}} {{coinTicker}}</div>
                <div class="subtitle">{{$localizeFilter('choose_network')}}</div>
                <div class="network" v-for="(network, idx) in networksForCoin" :key="idx" @click="selectNetwork(network)">
                    <div class="circle"></div>
                    <div class="network-name">{{$localizeFilter('network') + ' '}} {{$store.getters.networkByTicker(network).networkName}}</div>
                </div>
                <div class="text">{{$localizeFilter('choose_network_confirm')}}</div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        coinTicker:{
            type:String,
            required:true
        }
    },
    setup() {
        
    },
    data: ()=>({
    }),
    methods: {
        closeModal() {
            this.$emit('closeModal')
        },
        async selectNetwork(network) {
            await this.$store.dispatch('setFillWalletNetwork',{network})
            this.$emit('networkSelected', network)
        }
    },
    computed: {
        networksForCoin() {
            return this.$store.getters.networksForCoin(this.coinTicker)
        },
    },
})
</script>


